* {
  padding: 0 ;
  margin: 0;
  box-sizing: border-box;
}


section#wrapper {
  width: 600px;
  height: 400px;
  margin: 100px auto;
  border: 3px solid #b76c07;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f1d4a6;
  .window {
    width: 300px;
    height: 300px;
    border: 1px solid #e5a24c;
    position: relative;
    overflow: hidden;
    img {
      display: block;
      width: 100%;

      position: absolute;

      &#i1 {
        z-index: 7;
        animation: animate1 8s forwards   ease-in-out;
        //animation-delay: 5s;
        animation-iteration-count: infinite;
        opacity: 0;

      }

        &#i2 {
          z-index: 6;
          animation:animate1  8s  forwards  ease-in-out;
          animation-delay: 4s;

          animation-iteration-count: infinite;
          opacity: 0;

        }
        //&#i3 {
        //  z-index: 5;
        //  animation: animate1  4s  forwards 12s ease-in-out;
        //  animation-iteration-count: infinite;
        //  opacity: 0;
        //
        //
        //}
      }
      //&#i4 {
      //  z-index: 3;
      //  animation: animate1  4s  forwards 12s ease-in-out;
      //  animation-iteration-count: infinite;
      //  opacity: 0;
      //
      //}
      //&#i5 {
      //  z-index: 2;
      //  animation: animate1  4s  forwards 14s ease-in-out;
      //  animation-iteration-count: infinite;
      //  opacity: 0;
      //
      //}
      //&#i6 {
      //  z-index: 1;
      //  animation: animate1 4s  forwards  16s ease-in-out;
      //  animation-iteration-count: infinite;
      //  opacity: 0;
      //}
      @keyframes animate1 {
        0%  {
          left: 0;
          opacity: 0;
        }
        50% {
          opacity: 1;
          transform: translateX(0);

        }
        80% {

          opacity: 0;
          filter: grayscale(0.5);

        }
        85%{
          opacity: 0;
          filter: grayscale(0.5);

        }



        100% {


          transform: translateX(-300px);
          filter: none;

          opacity: 0;
        }
      }


    }


  }

